body {
  margin: 0;
  padding: 0;
  min-width: 1200px;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 23px;
  color: #636363;
  background-image: url("../../img/bg.png"), url("../../img/bg-2.png"), url("../../img/bg-3.png");
  background-repeat: no-repeat;
  background-position: top right;
}

a {
  color: #636363;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #636363;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.city-list,
.site-navigation {
  list-style: none;
  padding: 0;
  margin: 0;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 1180px;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
}

.container-wide {
  width: 1420px;
}

.container-fluid {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container-fluid {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container-fluid {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container-fluid {
    padding: 0 16px;
  }
}

.main-header-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}
.main-header-city-list {
  margin-right: 80px;
}
.main-header-register-button {
  margin-left: auto;
  margin-top: 35px;
}

.city-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0 25px;
  width: 400px;
}
.city-list-item {
  padding-top: 40px;
  padding-bottom: 20px;
}
.city-list-link {
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
  text-transform: uppercase;
}
.city-list-link--active {
  color: #2f80ed;
}

.site-navigation {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 0 40px;
  width: 520px;
}
.site-navigation-item {
  padding: 40px 0 20px;
}
.site-navigation-link {
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
}

.button {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  border-radius: 50px;
  padding: 10px;
}
.button--register {
  color: #2f80ed;
  background-color: #ffffff;
}

